import React from "react";
import { Avatar } from "@material-ui/core";
import { green, blue } from "@material-ui/core/colors";
import moment from "moment";

const ChatSession = ({ setSession, sessions, session }) => {
  return (
    <React.Fragment>
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
          marginRight: 10,
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <RenderChatSession
          sessions={sessions}
          sessionId={session}
          onSelect={(sessionId) => {
            setSession(sessionId);
          }}
        ></RenderChatSession>
      </div>
    </React.Fragment>
  );
};

const RenderChatSession = ({ sessions, onSelect, sessionId }) => {
  return (
    <React.Fragment>
      {sessions.map((session, index) => {
        return (
          <SingleSession
            onClick={(id) => {
              onSelect(id);
            }}
            key={index}
            firstName={session.personNextSide.firstName}
            active={session.session === sessionId}
            id={session.session}
            profilephoto={session ? session.personNextSide.profilephoto : ""}
            LastName={session.personNextSide.LastName}
            online={session.personNextSide.Online.status === "Online"}
            lastSeen={session.personNextSide.Online.lastSeen}
          ></SingleSession>
        );
      })}
    </React.Fragment>
  );
};

const SingleSession = ({
  id,
  profilephoto,
  firstName,
  LastName,
  onClick,
  active,
  online,
  UnReadMessageCount,
  lastSeen,
}) => {
  return (
    <div
      style={{
        width: "100%",
        color: "black",
        cursor: "pointer",
        padding: 10,
        height: 100,
        background: active ? "rgba(0,0,0,0.05)" : "rgba(0,0,0,0.02)",
        display: "flex",
        transition: "0.5s ease",
        alignItems: "center",
      }}
      onClick={() => onClick(id)}
    >
      <div style={{ position: "relative", marginLeft: "10%", marginRight: 10 }}>
        <Avatar src={profilephoto}></Avatar>

        <div
          style={{
            position: "absolute",
            bottom: 0,
            left: 0,
            height: 10,
            width: 10,
            borderRadius: "50%",
            backgroundColor: online ? green[700] : "transparent",
            transform: online
              ? "translateY(-40px) translateX(25px)"
              : "translateY(0px) translateX(0px)",
            transition: "0.5s ease",
          }}
        ></div>
      </div>
      <div
        style={{
          fontFamily: "poppins",
          fontWeight: "bold",
        }}
      >
        <div>{firstName + " " + LastName}</div>
        {lastSeen ? <div style={{ fontSize: 10 }}>Last Seen</div> : ""}
        <div style={{ fontWeight: "lighter", fontSize: 12 }}>
          {lastSeen
            ? moment(lastSeen).format("MMMM Do YYYY, h:mm:ss a")
            : online
            ? "Online"
            : ""}
        </div>
      </div>
      {UnReadMessageCount ? (
        <div
          style={{
            height: 30,
            width: 30,
            background: blue[700],
            borderRadius: "50%",
            color: "white",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            marginLeft: "auto",
          }}
        >
          <div style={{ fontSize: 10 }}>{UnReadMessageCount}</div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default ChatSession;
