import url from "../../strings/url";
import React from "react";
import ChatWindow from "./ChatWindow";
import { Grid, CircularProgress, IconButton } from "@material-ui/core";
import ChatSessions from "./ChatSessions";
import { Responsive } from "semantic-ui-react";
import { Backspace, AddOutlined } from "@material-ui/icons";
import Socket from "socket.io-client";
import AddConversation from "./AddCoversation";
import { connect } from "react-redux";
import moment from "moment";
const Chat = (props) => {
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(false);
  const [sessions, setSessions] = React.useState([]);
  const [session, setSession] = React.useState();
  const [socket, setSocket] = React.useState(Socket.connect(`${url}/chat`));
  const [token, setToken] = React.useState(props.token);
  const [alreadyConnectedUsers, setACU] = React.useState({});
  const [OpenAddConversationModal, setOACM] = React.useState(false);

  const handleOnSomeoneOnline = (id) => {
    setSessions((prev) => {
      return prev.map((session) => {
        if (session.personNextSide.id === id) {
          return {
            ...session,
            personNextSide: {
              ...session.personNextSide,
              Online: { status: "Online", lastSeen: null },
            },
          };
        }
        return session;
      });
    });
  };
  const handleOnSomeoneOffline = (id) => {
    setSessions((prev) => {
      return prev.map((session) => {
        if (session.personNextSide.id === id) {
          return {
            ...session,
            personNextSide: {
              ...session.personNextSide,
              Online: { status: "Offline", lastSeen: new Date() },
            },
          };
        }
        return session;
      });
    });
  };

  const handleFetchSessions = () => {
    const headers = new Headers();
    setLoading(true);
    setSessions([]);
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    fetch(`${url}/getMySessions`, { method: "GET", headers })
      .then(async (res) => {
        try {
          if (res.ok) {
            const sessions = await res.json();
            console.log(sessions);
            sessions.sort((a, b) => {
              return -1 * moment(a.lastMessage).diff(b.lastMessage, "seconds");
            });

            setLoading(false);
            setSessions(sessions);

            for (let i = 0; i < sessions.length; i++) {
              const { id } = sessions[i].personNextSide;
              setACU((prev) => ({ ...prev, [id]: true }));
            }
            return;
          }
          setLoading(false);
          setError(true);
        } catch (e) {
          console.log(e.message);
        }
      })
      .catch((e) => {
        setLoading(false);
        setError(true);
      });
  };

  React.useEffect(() => {
    handleFetchSessions();
    socket.emit("Save_Id", {
      token: token,
      session: null,
    });
    socket.on("someOneGetsOnline", (data) => {
      handleOnSomeoneOnline(data.id);
    });
    socket.on("someOneGetsOffline", (data) => {
      handleOnSomeoneOffline(data.id);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  return (
    <React.Fragment>
      {OpenAddConversationModal ? (
        <AddConversation
          alreadyConnectedUsers={alreadyConnectedUsers}
          onClose={() => setOACM(false)}
          onSessionCreated={() => {
            handleFetchSessions();
          }}
        ></AddConversation>
      ) : (
        ""
      )}
      <Responsive {...Responsive.onlyComputer}>
        <Grid container>
          <Grid item sm={5}>
            <Conversations
              onAddConversation={() => {
                setOACM(true);
              }}
            ></Conversations>
            {loading ? (
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "50vh",
                }}
              >
                <CircularProgress style={{ color: "black" }}></CircularProgress>
              </div>
            ) : (
              ""
            )}
            {error ? (
              <div
                style={{
                  textAlign: "center",
                  marginTop: "10%",
                  fontFamily: "poppins",
                  fontWeight: "bold",
                }}
              >
                Error Fetching Conversations!!
              </div>
            ) : (
              ""
            )}
            <ChatSessions
              setSession={setSession}
              sessions={sessions}
              session={session}
            ></ChatSessions>
          </Grid>
          <Grid item sm={6}>
            {session ? (
              <ChatWindow
                socket={socket}
                session={session}
                onBack={() => {
                  setSession(null);
                }}
                history={props.history}
                personNextSide={
                  sessions.find((res) => {
                    return res.session === session;
                  }).personNextSide
                }
              ></ChatWindow>
            ) : (
              ""
            )}
          </Grid>
          <Grid item sm={1}></Grid>
        </Grid>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        {!session ? (
          <Conversations
            onAddConversation={() => {
              setOACM(true);
            }}
          ></Conversations>
        ) : (
          ""
        )}
        {session ? (
          <ChatWindow
            socket={socket}
            session={session}
            onBack={() => {
              setSession(null);
            }}
            history={props.history}
            personNextSide={
              sessions.find((res) => {
                return res.session === session;
              }).personNextSide
            }
          ></ChatWindow>
        ) : (
          <ChatSessions
            setSession={setSession}
            sessions={sessions}
            session={session}
          ></ChatSessions>
        )}
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <Grid container>
          <Grid item sm={5}>
            <Conversations
              onAddConversation={() => {
                setOACM(true);
              }}
            ></Conversations>
            {loading ? (
              <div
                style={{
                  height: "50vh",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <CircularProgress style={{ color: "black" }}></CircularProgress>
              </div>
            ) : (
              <ChatSessions
                setSession={setSession}
                sessions={sessions}
                session={session}
              ></ChatSessions>
            )}
          </Grid>
          <Grid item sm={7}>
            {session ? (
              <ChatWindow
                socket={socket}
                session={session}
                onBack={() => {
                  setSession(null);
                }}
                history={props.history}
                personNextSide={
                  sessions.find((res) => {
                    return res.session === session;
                  }).personNextSide
                }
              ></ChatWindow>
            ) : (
              ""
            )}
          </Grid>
        </Grid>
      </Responsive>
    </React.Fragment>
  );
};

const Conversations = ({ onAddConversation }) => {
  return (
    <div
      style={{
        fontWeight: "bold",
        fontSize: 30,
        padding: 20,
        fontFamily: "poppins",
        display: "flex",
        justifyContent: "center",
      }}
    >
      <div style={{ flexGrow: 1, textAlign: "center" }}>Conversations</div>
      <IconButton
        onClick={() => {
          onAddConversation();
        }}
      >
        <AddOutlined></AddOutlined>
      </IconButton>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.userReducer.token,
  };
};
export default connect(mapStateToProps)(Chat);
